import Canvas from './canvas';


// add all the logic to the class
const random = (min, max) => (Math.random() * (max - min + 1)) + min;

const ball = (x, y) => {
  const data = {
    x,
    y,
    vx: random(-4, 4),
    vy: random(-10, -4)
  };

  window.Canvas.addBall(data);
}

window.Canvas = new Canvas(document.getElementById('canvas'));

